import { React, useState, useContext } from 'react'
import { DarkModeContext } from './DarkModeContext';

import jsPDF from 'jspdf';

function FormC() {
  const { darkMode } = useContext(DarkModeContext);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    // Add more form fields as needed
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Generate PDF with form data
    generatePDF();
  };

  const generatePDF = () => {
    const { name, email } = formData;
    // Use jspdf to create a new PDF instance
    const pdf = new jsPDF();

    // Set the content of the PDF
    pdf.text(20, 20, `Name: ${name}`);
    pdf.text(20, 30, `Email: ${email}`);
    // Add more content as needed

    // Save the PDF
    pdf.save('form_data.pdf');
  };
  return (
    <>
      <section className="text-gray-600 body-font">
        <div className="container px-5 py-24 mx-auto flex flex-wrap items-center">
          <div className="lg:w-3/5 md:w-1/2 md:pr-16 lg:pr-0 pr-0">
            <h1 className={`title-font text-center font-medium text-3xl text-purple-800  ${darkMode ? 'text-white' : ' text-purple-800'}`}>Contact Us for building your future</h1>
            <p className={`leading-relaxed mt-4 text-center md:text-left ${darkMode ? 'text-white' : 'text-gray'}`}>Poke slow-carb mixtape knausgaard, typewriter street art gentrify hammock starladder roathse. Craies vegan tousled etsy austin.</p>
          </div>
          <form onSubmit={handleSubmit} className="lg:w-2/6 md:w-1/2 bg-gray-100 rounded-lg p-8 flex flex-col md:ml-auto w-full mt-10 md:mt-0">
            <h2 className="text-gray-900 text-lg font-medium title-font mb-5">Sign Up</h2>
            <div className="relative mb-4">
              <label for="full-name" className="leading-7 text-sm text-gray-600">Full Name</label>
              <input value={formData.name} onChange={handleChange} type="text" id="name" name="name" className="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
            </div>
            <div className="relative mb-4">
              <label for="email" className="leading-7 text-sm text-gray-600">Email</label>
              <input value={formData.email} onChange={handleChange} type="email" id="email" name="email" className="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
            </div>
            <button type="submit" className="text-white bg-purple-800 border-0 py-2 px-8 focus:outline-none hover:bg-purple-600 rounded text-lg">submit</button>
            <p className="text-xs text-gray-500 mt-3">Literally you probably haven't heard of them jean shorts.</p>
          </form>
        </div>
      </section>



    </>
  )
}

export default FormC
